let that;
import Bmob from "hydrogen-js-sdk";
import wInput from '../../../../components/watch-login/watch-input.vue' //input
import wButton from '../../../../components/watch-login/watch-button.vue' //button

export default {
	data() {
		return {
			//logo图片 base64
			phoneData: '', // 用户/电话
			passData: '', //密码
			isRotate: false, //是否加载旋转
			user: uni.getStorageSync("user")
		}
	},
	components: {
		wInput,
		wButton,
	},
	mounted() {
		that = this;
		
		that.phoneData = that.user.username
		that.passData = that.user.pwd?that.user.pwd:''
	},
	methods: {
		startReg() {
			//注册
			if (that.isRotate) {
				//判断是否加载中，避免重复点击请求
				return false;
			}
			if (that.phoneData.length < 8) {
				uni.showToast({
					icon: 'none',
					position: 'bottom',
					title: '账号不能少于8位'
				});
				return false;
			}
			
			if (that.passData.length < 6) {
				uni.showToast({
					icon: 'none',
					position: 'bottom',
					title: '密码不能少于6位'
				});
				return false;
			}
			that.isRotate = true

			const query = Bmob.Query('_User');
			query.set('username', that.phoneData)
			query.set('password', that.passData)
			query.set('pwd', that.passData)
			query.set('id', that.user.objectId) //需要修改的objectId
			query.save().then(res => {
				that.user.pwd = that.passData
				uni.showToast({
					title:"设置成功",
					icon:'none'
				})
				that.isRotate = false
			}).catch(err => {
				uni.showToast({
					title:"该账号已存在",
					icon:'none'
				})
				that.isRotate = false
				console.log(err)
			})
		}
	}
}
